body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.nav {
  display: none;
  flex-flow: column;
}

@media (min-width: 578px) {
  .nav {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
  }
}

.nav_visible {
  display: flex;
}

.nav__menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  flex-flow: column;
}

@media (min-width: 578px) {
  .nav__menu {
    flex: 1 0 auto;
    flex-flow: row;
    justify-content: space-between;
  }
}

.nav__item {
  border-bottom: 1px dotted #c1b2b1;
  transition: 300ms;
}

@media (min-width: 578px) {
  .nav__item {
    border: none;
  }
}

.nav__link {
  display: block;
  padding: 0.5em 0.7em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: #c1b2b1;
  transition: 300ms;
}

.nav__link:hover {
  background-color: #c1b2b1;
  color: #ffffff;
}

.nav__search {
  display: flex;
  align-items: flex-end;
  font-size: 1.3em;
  padding-bottom: 0;
}

@media (min-width: 578px) {
  .nav__search {
    padding: 0 0 0 0.4em;
    margin-left: 1em;
  }
}

.button {
  display: inline-block;
  padding: 0.7em 2em;
  border: 2px solid #ffffff;
  border-radius: 2em;
  background-color: rgba(255, 255, 255, 0.2);
  font-family: inherit;
  font-size: inherit;
  line-height: 1em;
  color: inherit;
  transition-duration: 300ms;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.4);
  cursor: pointer;
}

.button_nav {
  padding: 0.5em 1em;
  border: 2px solid #c1b2b1;
}

.button_nav:hover {
  background-color: #c1b2b1;
  color: #ffffff;
}

.button_triangle {
  padding: 0;
  width: 5em;
  height: 5em;
  border-radius: 100%;
}

.button_triangle::before {
  content: url("../img/arrow-triangle.svg");
}

.title {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-size: 70px;
  text-transform: uppercase;
  color: #4e4e4e;
  transition: 300ms;
}

@media (min-width: 578px) {
  .title {
    font-size: 80px;
  }
}

@media (min-width: 768px) {
  .title {
    font-size: 90px;
  }
}

@media (min-width: 1024px) {
  .title {
    font-size: 100px;
  }
}

.title::before {
  content: '';
  display: block;
  height: 0.4em;
  background: url("../img/divider-title.svg") no-repeat;
  background-size: 1.2em;
}

.author {
  display: flex;
  align-items: center;
  font-size: 20px;
  color: #9c9c9c;
}

.author__avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 1em;
}

.more {
  display: flex;
  font-size: 25px;
  color: #98b8f6;
  transition: 300ms;
}

.more::after {
  content: url("../img/arrow-right.svg");
  display: inline-block;
  margin-left: 0.8em;
  padding-top: 0.1em;
  transition: 450ms;
}

.more:hover:after {
  margin-left: 0.2em;
  transform: rotate(360deg);
}

